.Range{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:265px;line-height:15px;height:45px;border:1px solid #000;background:#fff;color:#000;border-radius:8px;box-sizing:border-box;cursor:pointer}.Range--dark{border:1px solid #fff;background:#0c0c0c;color:#fff}.Range--open{border-bottom:1px solid rgba(0,0,0,0);border-radius:8px 8px 0 0}.Range__title{margin:14px 0 15px 14px}.Range__arrow{position:absolute;display:flex;right:18px;float:right;fill:#b3b3b3}.Range__сontainer{display:block;position:absolute;justify-content:center;align-items:center;align-self:center;width:inherit;top:45px;border:1px solid #000;padding:20px 22px;box-sizing:border-box;border-radius:0 0 8px 8px;background:#fff;z-index:1}.Range__сontainer--dark{border:1px solid #fff;background:#000;color:#fff}.Range__сontainer--open{border-top:none;display:flex}.Range__title{visibility:hidden;line-height:0}.Range__title:after{visibility:visible;content:"Created";display:block;position:absolute}@media screen and (min-width: 1024px)and (max-width: 1365px){.Range{width:220px}}@media screen and (min-width: 768px)and (max-width: 1023.5px){.Range{width:160px}}@media screen and (min-width: 320px)and (max-width: 767px){.Range{width:280px}}